
import {mapMutations, mapState} from "vuex";

export default {
  name: "IPFilter",
  computed: {
    localHoods: {
      get() {
        return this.filteredHoods;
      },
      set(newValue) {
        this.setFilteredHoods({newFilteredHoods: newValue});
      }
    },

    localAuthors: {
      get() {
        return this.filteredAuthors;
      },
      set(newValue) {
        console.log(newValue);
        this.setFilteredAuthors({newFilteredAuthors: newValue});
      }
    },

    ...mapState([
        'authors',
        'filteredAuthors',
        'hoods',
        'filteredHoods'
    ])
  },
  methods: {
    ...mapMutations([
        'setFilteredHoods',
        'setAllFilteredHoods',
        'unsetAllFilteredHoods',
        'setAllFilteredAuthors',
        'unsetAllFilteredAuthors',
        'setFilteredAuthors',
        'setAllFilters',
        'unsetAllFilters'
    ])
  }
}
